<template>
	<div class="black-bg" v-if="isModalOpen == true">
    <div class="white-bg">
      <h5>{{ rooms[pnum].title }}</h5>
      <img :src="rooms[pnum].image" style="width:100%">
      <p>{{ rooms[pnum].content }}</p>
      <input v-model.number="month">
      <p>{{month}}개월 선택함 : {{ rooms[pnum].price * month }}원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
	name : 'Modal',
  data() {
    return {
      month : '',
    }
  },
  watch : {
    month(a) {  //데이터 이름으로 함수를 만들어야 한다 파라미터 하나는 변경된 month, 뒤에 오는 파라미터는 변경 전 month
      if(a>12) {
        alert('12개월까지만 가능합니다.');
        this.month = 3;
      }
      if(isNaN(a) || a == ' ')  {
        alert('숫자만 입력해 주세요.');
        this.month = 3;
      }
    }
  },
	props : {
    rooms : Object,
    pnum : Number,
    isModalOpen : Boolean
  },
  updated(){
    if(this.month < 3){
      alert('3개월부터 가능합니다.');
      this.month=3;
    }
  }
}
</script>

<style>
.black-bg {
  width:100%; height:100%;
  background: rgba(0,0,0,0.5);
  position: fixed; padding: 20px;
}
.white-bg {
  width:100%;
  background:white;
  border-radius: 8px;
  padding:20px;
}
</style>