<template>
  <!-- <div class="start" :class="{end : isModalOpen}">  //isModalOpen이 true일 때만 .end 부착
    <Modal @closeModal="isModalOpen=false" :rooms="rooms" :pnum="pnum" :isModalOpen="isModalOpen" />
  </div> -->
  <transition name="fade">
    <Modal @closeModal="isModalOpen=false" :rooms="rooms" :pnum="pnum" :isModalOpen="isModalOpen" />
  </transition>
  <div class="menu">
    <a v-for="a in menus" :key="a">{{ a }}</a>
  </div>
  <Discount v-if="showDiscount == true" :discountRate="discountRate"/>
  <select v-model="sortId" @change="productSort(sortId)">
    <option value="0">상품 정렬</option>
    <option value="1">가격 오름차순</option>
    <option value="2">가격 내림차순</option>
    <option value="3">상품명 가나다순</option>
  </select>
  <Card @openModal="isModalOpen=true; pnum=$event" v-for:="(oneRoom, i) in rooms" :key="oneRoom" :oneRoom="oneRoom" />
</template>
<script>

import data from './assets/data.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';

export default {
  name: 'App',
  data(){
    return {
      sortId : 0,
      pnum : 0,
      rooms : data, //정렬한 데이터가 필요할 때
      roomsOg : [...data],  //별개 사본 생성 가능
      isModalOpen: false,
      reports : [0,0,0,0,0,0],
      menus : ['Home', 'Products', 'About'],
      showDiscount : true,
      discountRate : 30
    }  
  },
  methods : {
    increase(i){
      this.reports[i]++;
    },
    productSort(sortId){
      switch(sortId) {
        case '0':
          this.rooms = [...this.roomsOg];  //등호로 array를 넣으면 왼쪽, 오른쪽 값을 공유한다는 의미.
          break
        case '1' :
          this.rooms.sort(function(a,b){  //sort는 원본을 변형시킴. 원본데이터를 보존하는 것이 유행.
            return a.price - b.price
          })
          break;
        case '2' :
          this.rooms.sort(function(a,b){
            return b.price - a.price
          })
          break;
        case'3' :
          this.rooms.sort(function(a,b){
            return a.title < b.title? -1 :a.title > b.title ? 1 : 0;
          })
          break;
      }
    },
  },
  mounted(){
    setInterval(()=>{
      if(this.discountRate>0)
        this.discountRate--;
    },1000);
  },
  // mounted(){
  //   setTimeout(()=>{    //this를 쓰기 위해서는 arrow function 사용해야 한다
  //     this.showDiscount = false;
  //   }, 2000);
  // },
  components: {
    Discount : Discount,
    Modal : Modal,
    Card : Card,
  }
}
</script>

