body {
  margin : 0
}

div {
  box-sizing: border-box;
}

.room-img {
  width:40%;
  margin-top: 40px;;
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.menu{
  background: darkslateblue;
  padding: 15px;
  border-radius: 5px;
}

.menu a{
  color: white;
  padding: 10px;
}

.fade-enter-from {  /*시작 스타일*/
  transform: translateY(-1000px);
}
.fade-enter-active {
  transition: all 1s;
}
.fade-enter-to {  /*종료 스타일*/
  transform: translateY(0px);
}

.fade-leave-from {  /*시작 스타일*/
  opacity: 1;
}
.fade-leave-active {
  transition: all 1s;
}
.fade-leave-to {  /*종료 스타일*/
  opacity: 0;
}

.start {
  opacity: 0;
  transition: all 1s;
}

.end {
  opacity: 1;
}
</style>
